import React, { useState, useEffect } from 'react';


function Menu(props){
    const [count, calcCount] = useState(0);

    useEffect(() => {
        const cartDisplay = document.getElementsByClassName('circle')[0];
        cartDisplay.textContent = count;
    }, [count]);

    return(
        <div className="menublock">
            <div className="foods">{props.food} <span>{props.price}</span></div>
            <p className="food-details">{props.details}</p> 
            <form id="menuform" action="">
            <select name="quantity" id="quantity">
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
            </select>
            <button onClick={() => calcCount(count +1)}>Add to Cart</button>
            </form>
        </div>
    )
}

export default Menu