import React from 'react';
import $ from "jquery";


function Close() {
    
    const close = () => {
        $('.displaymenu').slideUp(1000).hide(1000);
        $('.displaymenutext').slideUp(1000).hide(1000);
        $('.menublock').hide(1000);
        $('.contactblock').hide(1000);
        document.getElementsByClassName('close')[0].style.display = "none";
    }

    return (
        <span className="close" onClick={close}>close</span>
    ) 
}

export default Close